@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap");
* {
	margin: 0;
	padding: 0;
	outline: none;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
}
.wrappingDiv {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	padding: 40px;
	background: url("../../../../images/formBck.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	padding-top: 120px;
}
.addAdv-container {
	max-width: 800px;
	background: rgba(255, 255, 255, 0.918);
	width: 800px;
	padding: 25px 40px 10px 40px;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
.addAdv-container .text {
	text-align: center;
	font-size: 35px;
	font-weight: 600;
	background: orange;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.addAdv-container form {
	padding: 30px 0 0 0;
}
.addAdv-container form .form-row {
	display: flex;
	margin: 52px 0;
}
form .form-row .input-data {
	width: 100%;
	height: 40px;
	margin: 0 20px;
	position: relative;
}
form select {
	width: 100%;
	height: 40px;
	margin: 0 20px;
	position: relative;
	border: 2px solid orange;
}
form .form-row .textarea {
	height: 70px;
}
.input-data input,
.textarea textarea {
	display: block;
	background: rgba(255, 255, 255, 0.582);
	width: 100%;
	height: 100%;
	border: none;
	font-size: 17px;
	border-bottom: 2px solid rgba(0, 0, 0, 0.12);
}
.input-data input:focus ~ label,
.textarea textarea:focus ~ label,
.input-data input:valid ~ label,
.textarea textarea:valid ~ label {
	transform: translateY(-20px);
	font-size: 14px;
	color: orange;
}
.textarea textarea {
	resize: none;
	padding-top: 10px;
}
.input-data label {
	position: absolute;
	pointer-events: none;
	bottom: 10px;
	font-size: 16px;
	transition: all 0.3s ease;
}
.textarea label {
	width: 100%;
	bottom: 40px;
	background: #fff;
}
.input-data .underline {
	position: absolute;
	bottom: 0;
	height: 2px;
	width: 100%;
}
.input-data .underline:before {
	position: absolute;
	content: "";
	height: 2px;
	width: 100%;
	background: orange;
	transform: scaleX(0);
	transform-origin: center;
	transition: transform 0.3s ease;
}
.input-data input:focus ~ .underline:before,
.input-data input:valid ~ .underline:before,
.textarea textarea:focus ~ .underline:before,
.textarea textarea:valid ~ .underline:before {
	transform: scale(1);
}
.submit-btn .input-data {
	overflow: hidden;
	height: 45px !important;
	width: 25% !important;
	margin: 0 auto;
}
.submit-btn .input-data .inner {
	height: 100%;
	width: 300%;
	position: absolute;
	left: -100%;
	background: orange;

	transition: all 0.4s;
}
.submit-btn .input-data:hover .inner {
	left: 0;
}
.submit-btn .input-data input {
	background: none;
	border: none;
	color: #fff;
	font-size: 17px;
	font-weight: 500;
	text-transform: uppercase;
	letter-spacing: 1px;
	cursor: pointer;
	position: relative;
	z-index: 2;
}

#overview-text-area {
	width: 100%;
	border: none;
	border-bottom: orange 3px solid;
}
@media (max-width: 700px) {
	.addAdv-container .text {
		font-size: 30px;
	}
	.addAdv-container form {
		padding: 10px 0 0 0;
	}
	.addAdv-container form .form-row {
		display: block;
	}
	form .form-row .input-data {
		margin: 35px 0 !important;
	}
	.submit-btn .input-data {
		width: 40% !important;
	}
}
