.navbar {
	background: #1c223700;
	height: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.2rem;
	position: sticky;
	top: 0;
	z-index: 999; /*   prethodni (sa bc bojom nezavistan od hero sekcije) */
	/*   margin-bottom: 7rem;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  z-index: 999; */
	/*Novi (Transparentni, predstavlja dio hero sekcije)*/
}

.navbar-absolute {
	position: absolute;
	width: 98vw;
	top: 0;
	left: 0;
}

.navbar-container {
	display: flex;
	justify-content: space-between;
	height: 80px;
}
.navbar-logo-div {
	background: url("./images/me.svg");

	width: 7.8rem;
	height: 5.2rem;

	/* height: 75%; */
	background-position: center;
	z-index: 2000;
	background-size: contain;
	background-repeat: no-repeat;
}

.container {
	z-index: 1;
	width: 100%;
	max-width: 1500px;
	margin-right: auto;
	margin-left: auto;
	padding-right: 50px;
	padding-left: 50px;
}

