@font-face {
  font-family: Bite;
  src: url(/react-final-task/static/media/BiteChocolate-2RGl.477f9866.ttf);
}
body {
  background: rgba(78, 78, 78, 0.022);
}
.hero-welcome {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(87, 87, 87, 0.664), rgba(0, 0, 0, 0.486)),
    url(/react-final-task/static/media/hrback8.c1c3f6e1.jpg) center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
a {
  text-decoration: none;
}
.hero-sub-headline {
  font-size: 6.3rem;
  font-family: Bite, cursive;
  color: #f8802bc0;
  font-weight: 200;
  line-height: 0.4;
  letter-spacing: 2px;
  opacity: 0;
  animation: fadeUp 0.5s forwards;
  animation-delay: 0.5s;
}
/*Make first letter twice bigger than the rest of the text*/
.hero-first-letter {
  font-family: Bite, cursive;
  text-transform: uppercase;
  font-size: 9.3rem;
}

.hero-headline {
  color: #fff;
  font-size: 3.7rem;
  font-family: "Source Sans Pro", sans-serif;
  letter-spacing: 0.5rem;
  /*   margin-right: -0.5rem;
 */
  animation: scale 0.5s forwards;
  margin: 0 -0.5rem 0 0;
}
.top-line {
  color: #fff;
  font-size: 1.3rem;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 7.4px;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.top-line span {
  color: #ff6b00;
}

.hero-separator {
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-line {
  width: 100%;
  max-width: 8.4rem;
  height: 0.25rem;
  background-color: #fff;
  position: relative;
  opacity: 0;
  animation: grow 2s forwards;
  animation-delay: 0.9s;
}
.hero-line-right::before,
.hero-line-left::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 0.5rem solid transparent;
  opacity: 0;
  animation: grow 0.5s forwards;
  animation-delay: 1.2s;
}
.hero-line-right::before {
  border-right-color: #fff;
  right: 0;
}
.hero-line-left::before {
  border-left-color: #fff;
  left: 0;
}
.hero-asterisk {
  font-size: 2.2rem;
  color: #fa7312;
  margin: 0 1.6rem;
  opacity: 0;
  animation: spin 0.5s forwards;
  animation-delay: 0.7s;
}
.hero-single-animation {
  opacity: 0;
  animation: fadeDown 0.5s forwards;
  animation-delay: 1.5s;
}
.hero-headline-description h5 {
  color: #fff;
  font-size: 1.4rem;
  font-weight: 100;
  text-transform: uppercase;
  margin-bottom: 1.2rem;
  letter-spacing: 3px;
  margin-right: -3px;
}
.btn {
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-right: -2px;
}
.cta-btn {
  font-size: 1.1rem;
  background-color: #fff;
  padding: 0.9rem 1.8rem;
  color: var(--body-font-color);
  border-radius: 0.4rem;
  transition: background-color 0.5s;
}
.cta-btn:hover,
.cta-btn:focus {
  color: #fff;
  background-color: var(--fav-font-color);
}

