/* .flip-card-container */
.flip-card-container {
	--hue: 25;
	--primary: hsl(var(--hue), 90%, 50%);
	--white-1: hsl(0, 0%, 90%);
	--white-2: hsl(0, 0%, 80%);
	--dark: hsl(var(--hue), 25%, 10%);
	--grey: hsl(0, 1%, 85%);

	width: 100%;
	height: 400px;
	cursor: pointer;
	perspective: 1000px;
}

/* .flip-card */
.flip-card {
	width: inherit;
	height: inherit;

	position: relative;
	transform-style: preserve-3d;
	transition: 0.6s 0.1s;
	margin: 0 auto;
}

/* hover and focus-within states */
.flip-card-container:hover .flip-card,
.flip-card-container:focus-within .flip-card {
	transform: rotateY(180deg);
}

/* .card-... */
.card-front,
.card-back {
	width: 100%;
	height: 100%;
	border-radius: 24px;

	background: var(--dark);
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;

	-webkit-backface-visibility: hidden;

	        backface-visibility: hidden;

	display: flex;
	justify-content: center;
	align-items: center;
}

/* .card-front */
.card-front {
	transform: rotateY(0deg);
	z-index: 2;
}

/* .card-back */
.card-back {
	transform: rotateY(180deg);
	z-index: 1;
}

/* figure */
figure {
	z-index: -1;
}

/* figure, .img-bg */
figure,
.img-bg {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

/* img */
img {
	height: 100%;
	border-radius: 24px;
	width: 100%;
	height: 100%;
	object-fit: cover; /*magic*/
}

/* figcaption */
figcaption {
	display: block;

	width: auto;
	margin-top: 12%;
	padding: 8px 22px;

	font-weight: bold;
	line-height: 1.6;
	letter-spacing: 2px;
	word-spacing: 6px;
	text-align: right;

	position: absolute;
	top: 0;
	right: 12px;

	color: var(--white-1);
	background: hsla(var(--hue), 25%, 10%, 0.5);
}

/* .img-bg */
.img-bg {
	background: hsla(var(--hue), 25%, 10%, 0.35);
}

.card-front .img-bg {
	-webkit-clip-path: polygon(0 45%, 100% 65%, 100% 100%, 0 100%);
	        clip-path: polygon(0 45%, 100% 65%, 100% 100%, 0 100%);
}

.card-front .img-bg::before {
	content: "";

	position: absolute;
	top: 57%;
	left: 50%;
	transform: translate(-50%, -50%) rotate(13deg);

	width: 100%;
	height: 6px;
	/* border: 1px solid var(--primary); */
	border-left-color: transparent;
	border-right-color: transparent;

	transition: 0.1s;
}

/*.card-back .img-bg {
   clip-path: polygon(0 0, 100% 0, 100% 80%, 0 60%);
} 
}*/
/* hover state */
.flip-card-container:hover .card-front .img-bg::before {
	/* width: 6px;
	border-left-color: transparent;
	border-right-color: transparent; */
}

/* ul */
.flip-card-container ul {
	/* padding-top: 60%; */
	/* margin: 0 auto; */
	width: 70%;
	height: 100%;

	list-style: none;
	color: var(--white-1);

	display: flex;
	justify-content: flex-end;
	align-items: flex-start;
	flex-direction: column;
}

/* li */
.flip-card-container li {
	width: 100%;
	margin-top: 12px;
	padding-bottom: 12px;

	font-size: 14px;
	text-align: left;

	position: relative;
}

.flip-card-container li:nth-child(2n) {
	color: var(--white-2);
}

.flip-card-container li:not(:last-child)::after {
	content: "";

	position: absolute;
	bottom: 0;
	left: 0;

	width: 100%;
	height: 1px;

	background: currentColor;
	opacity: 0.2;
}

/* button */
.flip-card-container button {
	font-family: inherit;
	font-weight: bold;
	color: var(--white-1);

	letter-spacing: 2px;

	padding: 9px 20px;
	border: 1px solid var(--grey);
	border-radius: 1000px;
	background: transparent;
	transition: 0.3s;

	cursor: pointer;
}

.flip-card-container button:hover,
.flip-card-container button:focus {
	color: var(--primary);
	background: hsla(var(--hue), 25%, 10%, 0.2);
	border-color: currentColor;
}

.flip-card-container button:active {
	transform: translate(2px);
}

/* .design-container */
.design-container {
	--tr: 90;
	--op: 0.5;

	width: 100%;
	height: 100%;

	background: transparent;
	position: absolute;
	top: 0;
	left: 0;

	pointer-events: none;
}

/* .design */
.design {
	display: block;

	background: var(--grey);
	position: absolute;

	opacity: var(--op);
	transition: 0.3s;
}

.design--1,
.design--2,
.design--3,
.design--4 {
	width: 1.5px;
	height: 100%;
}

.design--1,
.design--2 {
	top: 0;
	transform: translateY(calc((var(--tr) - (var(--tr) * 2)) * 1%));
