	margin: 0 0%;
	transition: 0.3s ease;
}
@media screen and (max-width: 512px) {
	.included-navigation .included-navigation .content {
		margin: 0 4%;
		margin-top: 5%;
	}
}
/* Tabs menu */
.included-navigation .tabs {
	margin: 0;
	background-image: linear-gradient(135deg, #ff7240 0%, #ff7240 100%);
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 80px;
	border-radius: 2px 2px 0px 0px;
}
@media screen and (max-width: 512px) {
	.included-navigation .tabs {
		height: 40px;
	}
}
/* Tab Links */
.included-navigation .tablinks {
	background: transparent;
	background-image: linear-gradient(
		90deg,
		transparent 100%,
		rgba(255, 255, 255, 0.2) 100%
	);
	border: none;
	outline: none;
	cursor: pointer;
	width: 25%;
	/*change depending on the number of tabs*/
	height: 80px;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 16px;
	font-family: "IBM Plex Sans", sans-serif;
	overflow: hidden;
	transition: 0.3s ease;
}
@media screen and (max-width: 512px) {
	.included-navigation .tablinks {
		height: 40px;
		font-size: 12px;
	}
}
.included-navigation .tablinks:before {
	/* Gradient */
	background-image: linear-gradient(135deg, #ff7240 0%, #ff7240 100%);
	content: "";
	width: 100%;
	height: 0px;
	position: absolute;
	top: 0;
	left: 0;
	transition: 0.3s ease-in-out;
	z-index: 2;
}
/*tab buttons hover*/
.included-navigation .tablinks:hover::before {
	height: 100%;
	z-index: 2;
	bottom: 0;
}
@media screen and (max-width: 512px) {
	.included-navigation .tablinks:hover::before {
		height: 0;
	}
}
/* Tab active */
.included-navigation .tablinks.active {
	background-color: white;
	z-index: 0;
	border-right: 0px;
	border-left: 0px;
	height: 110px;
	bottom: 0px;
	overflow: hidden;
}
@media screen and (max-width: 512px) {
	.included-navigation .tablinks.active {
		height: 60px;
	}
}
.included-navigation .tablinks.active:before {
	/* Gradient */
	content: "";
	width: 100%;
	height: 5px;
	top: 0;
	left: 0;
}
/* Tabs text */
.included-navigation .tablinks.active p,
.included-navigation .tablinks.active:hover p {
	opacity: 1;
	background: -webkit-linear-gradient(135deg, #ff7240 0%, #ff7240 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.included-navigation .tablinks p {
	opacity: 0.6;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 2;
	transition: 0.5s ease;
	padding: 0;
	margin: 0;
	color: #686868;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	font-weight: 400;
}
.included-navigation .tablinks:hover p {
	color: white;
	opacity: 1;
}
@media screen and (max-width: 512px) {
	.included-navigation .tablinks:hover p {
		color: #686868;
		opacity: 0.6;
	}
}
/* Tabs text bigger */
.included-navigation .tablinks p:before {
	content: attr(data-title);
	position: absolute;
	height: auto;
	width: auto;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 0.1;
	font-size: 40px;
	transition: 1s ease-out;
	z-index: -1;
	font-weight: 600;
	top: 110%;
}
@media screen and (max-width: 512px) {
	.included-navigation .tablinks p:before {
		display: none;
	}
}
.included-navigation .tablinks:hover p:before {
	opacity: 0.1;
	font-size: 40px;
	top: -80%;
}
/* tab content */
.included-navigation .wrapper_tabcontent {
	background-color: white;
	margin-top: 15px;
	z-index: -3;
	position: relative;
	opacity: 1;
	padding: 0px 60px;
	overflow: hidden;
	transition: all 1s ease;
	top: 0;
}
.included-navigation .tabcontent {
	display: none;
	min-height: 180px;
}
@keyframes tabEffect {
	from {
		top: -40px;
	}
	to {
		top: 0px;
	}
}
.included-navigation .tabcontent.active {
	transition: all 1s ease;
	display: block;
}
/* Tab content line */
.included-navigation .wrapper_tabcontent:after {
	content: "";
	height: 5px;
	width: 100%;
	position: absolute;
	background-image: linear-gradient(135deg, #ff7240 0%, #ff7240 100%);
	left: 0;
	bottom: 0;
	z-index: -2;
	transition: all 1s ease;
}
/* Title */
.included-navigation .tabcontent h3 {
	font-size: 28px;
	top: 75px;
	transform: rotate(90deg);
	position: absolute;
	left: -90px;
	opacity: 0.1;
	width: 200px;
	height: 60px;
	background: -webkit-linear-gradient(135deg, #667eea 0%, #764ba2 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	padding: 0;
	animation: city 1s ease;
}
@media screen and (max-width: 512px) {
	.included-navigation .tabcontent h3 {
		top: 65px;
	}
}
@keyframes city {
	from {
		left: -150px;
	}
	to {
		left: -90px;
	}
}
/* Text*/
.included-navigation .tabcontent p,
.included-navigation .tabcontent li {
	color: #3e3e3e;
	margin: 0;
	padding: 0;
	line-height: 28px;
	font-weight: 400;
	transition: all 1s ease;
	animation: fadeEffect 0.6s ease;
	width: 100%;
	font-size: 16px;
	font-family: "IBM Plex Sans", sans-serif;
}
@media screen and (max-width: 512px) {
	.included-navigation .tabcontent p {
		font-size: 14px;
		line-height: 26px;
	}
}
@keyframes fadeEffect {
	from {
		opacity: 0;
		margin-left: 30px;
	}
	to {
		opacity: 1;
		margin-left: 0;
	}
}

